import * as React from "react";
import { useQuery, useMutation } from "@apollo/client";
import { GET_USERS, UPDATE_USER, DELETE_USER } from "../graphql/users";
import Paper from "@mui/material/Paper";
import Table from "@mui/material/Table";
import Button from "@mui/material/Button";
import TableRow from "@mui/material/TableRow";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import CircularProgress from "@mui/material/CircularProgress";
import TableHead from "@mui/material/TableHead";
import Typography from "@mui/material/Typography";
import TableContainer from "@mui/material/TableContainer";
import TablePagination from "@mui/material/TablePagination";

import { columns, emptyUser } from "./common";
import DeleteDialog from "./DeleteDialog";
import EmployeeTableRow from "./EmployeeTableRow";
import EmployeeEditableTableRow from "./EmployeeEditableTableRow";

export default function EmployeePage() {
	const { loading, error, data, refetch } = useQuery(GET_USERS);
	const [
		updateUser,
		{
			loading: updateUserLoading,
			error: updateUserError,
			data: updateUserData,
		},
	] = useMutation(UPDATE_USER);

	const [
		deleteUser,
		{
			loading: deleteUserLoading,
			error: deleteUserError,
			data: deleteUserData,
		},
	] = useMutation(DELETE_USER);

	const [addingNewUser, setAddingNewUser] = React.useState(false);
	const [editableRowId, setEditableRowId] = React.useState(null);
	const isEditableRow = (id) => {
		return editableRowId === id;
	};

	const startEditingRow = (id) => {
		if (!id || editableRowId) return;
		setEditableRowId(() => id);
	};
	const cancelEditingRow = (id) => {
		if (id !== editableRowId) return;
		if (id === emptyUser.id) setAddingNewUser(false);
		setEditableRowId(null);
	};
	const saveRow = async (data) => {
		if (editableRowId !== data.id) return;
		await updateUser({
			variables: {
				input: { ...data, id: data.id === emptyUser.id ? "" : data.id },
			},
		});
		await refetch();
		setEditableRowId(null);
		if (data.id === emptyUser.id) {
			setAddingNewUser(false);
		}
	};
	const [deleteRowId, setDeleteRowId] = React.useState(null);
	const tryDeleteRow = (id) => {
		if (deleteRowId) return;
		setDeleteRowId(id);
	};
	const deleteRow = async () => {
		await deleteUser({
			variables: {
				id: deleteRowId,
			},
		});
		setDeleteRowId(null);
		setEditableRowId(null);
		await refetch();
	};

	const [page, setPage] = React.useState(0);
	const [rowsPerPage, setRowsPerPage] = React.useState(10);

	const isLoading = () => loading || updateUserLoading || deleteUserLoading;

	const handleChangePage = (event, newPage) => {
		setPage(newPage);
	};

	const handleChangeRowsPerPage = (event) => {
		setRowsPerPage(+event.target.value);
		setPage(0);
	};
	return (
		<>
			{!isLoading() && (
				<div>
					<Typography
						variant="h4"
						align="center"
						component="div"
						href="/"
						sx={{
							fontFamily: "monospace",
							fontWeight: 700,
							margin: "auto",
						}}
					>
						 
					</Typography>
					<div
						style={{
							display: "flex",
							marginRight: 10,
							justifyContent: "flex-end",
						}}
					>
						<div>
							<Button
								onClick={() => {
									setAddingNewUser((oldState) => !oldState);
									setEditableRowId(emptyUser.id);
								}}
								color={addingNewUser ? "secondary" : "primary"}
								variant="contained"
							>
								 
							</Button>
						</div>
					</div>
					<Paper sx={{ width: "100%", overflow: "hidden" }}>
						<TableContainer sx={{}}>
							<Table stickyHeader aria-label="sticky table">
								<TableHead>
									<TableRow>
										{columns.map((column) => (
											<TableCell
												key={column.id}
												align={column.align}
												style={{ minWidth: column.minWidth }}
											>
												{column.label}
											</TableCell>
										))}
									</TableRow>
								</TableHead>
								<TableBody>
									{addingNewUser && (
										<EmployeeEditableTableRow
											key={emptyUser.id}
											row={emptyUser}
											actions={{
												saveRow: saveRow,
												cancelEdit: cancelEditingRow,
											}}
										/>
									)}
									{data.users
										.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
										.map((row) => {
											return (
												<>
													{!isEditableRow(row.id) && (
														<EmployeeTableRow
															key={row.id}
															row={row}
															buttonsActive={!editableRowId}
															actions={{
																editRow: startEditingRow,
																tryDeleteRow: tryDeleteRow,
															}}
														/>
													)}
													{isEditableRow(row.id) && (
														<EmployeeEditableTableRow
															key={row.id}
															row={row}
															actions={{
																saveRow: saveRow,
																tryDeleteRow: tryDeleteRow,
																cancelEdit: cancelEditingRow,
															}}
														/>
													)}
												</>
											);
										})}
								</TableBody>
							</Table>
						</TableContainer>
						<TablePagination
							rowsPerPageOptions={[10, 25, 100]}
							component="div"
							count={data.users.length}
							rowsPerPage={rowsPerPage}
							page={page}
							onPageChange={handleChangePage}
							onRowsPerPageChange={handleChangeRowsPerPage}
						/>
					</Paper>
				</div>
			)}
			{isLoading() && (
				<CircularProgress
					size={24}
					sx={{
						position: "absolute",
						top: "50%",
						left: "50%",
						// marginTop: "-12px",
						// marginLeft: "-12px",
					}}
				/>
			)}
			<DeleteDialog
				open={!!deleteRowId}
				cancelAction={() => setDeleteRowId(null)}
				confirmAction={deleteRow}
			/>
			{/* {showUpdateAlert && (
				<Alert
					severity="error"
					sx={{
						// height: 20,
						position: "absolute",
						// top: "100%",
						// left: "50%",
						// marginTop: "-30px",
						// marginLeft: "-12px",
						right: 2,
						bottom: 2,
					}}
				>
					{updateUserError.message}
				</Alert>
			)} */}
		</>
	);
}
