import * as React from "react";
import Button from "@mui/material/Button";
import TableRow from "@mui/material/TableRow";
import TableCell from "@mui/material/TableCell";
import TextField from "@mui/material/TextField";
import InputLabel from "@mui/material/InputLabel";
import ButtonGroup from "@mui/material/ButtonGroup";
import MenuItem from "@mui/material/MenuItem";
import FormControl from "@mui/material/FormControl";
import Select from "@mui/material/Select";
import CloseIcon from "@mui/icons-material/Close";
import SaveIcon from "@mui/icons-material/Save";
import DeleteIcon from "@mui/icons-material/Delete";

import { columns } from "./common";
import { UserRoles } from "./user-roles.enum";

export default function EmployeeEditableTableRow({ row, actions }) {
	const roles = Object.values(UserRoles);
	const [name, setName] = React.useState(row.name);
	const [role, setRole] = React.useState(row.role ? row.role : "");
	const [managerId, setManagerId] = React.useState(row.managerId);
	const [email, setEmail] = React.useState(row.email);

	const handleSaveRowClick = () => {
		actions.saveRow({
			id: row.id,
			name,
			email,
			role: role,
			managerId,
		});
	};

	const handleCancelEditRowClick = () => {
		actions.cancelEdit(row.id);
	};

	const handleDeleteRowClick = () => {
		actions.tryDeleteRow(row.id);
	};

	const textInput = ({ value, setValue }) => (
		<TextField
			inputRef={(input) => input}
			sx={{
				width: "16ch",
				"& .Mui-focused": {
					width: "22ch",
				},
				"& .MuiInputBase-input": {
					width: "100%",
					height: 0,
				},
			}}
			variant="outlined"
			onChange={(event) => setValue(event.target.value)}
			value={value}
			inputProps={{ "aria-label": "value" }}
		/>
	);
	return (
		<>
			<TableRow hover role="checkbox" tabIndex={-1} key={row.id}>
				{columns.map((column) => (
					<TableCell key={column.id} align={column.align}>
						{column.id === "buttons" && (
							<div>
								<div style={{ display: "flex", gap: 15 }}>
									<ButtonGroup
										variant="outlined"
										aria-label="outlined button group"
									>
										<Button
											onClick={() => handleSaveRowClick()}
											disabled={!name || !email || !role}
										>
											<SaveIcon />
										</Button>
										<Button
											color="error"
											onClick={() => handleDeleteRowClick()}
											disabled={row.id === "new-user-id"}
										>
											<DeleteIcon />
										</Button>
										<Button
											color="error"
											onClick={() => handleCancelEditRowClick()}
										>
											<CloseIcon />
										</Button>
									</ButtonGroup>
								</div>
							</div>
						)}
						{column.id === "name" &&
							textInput({ value: name, setValue: setName })}
						{column.id === "role" && (
							<FormControl sx={{ m: 1, minWidth: 120 }} size="small">
								<InputLabel></InputLabel>
								<Select
									value={role}
									label=""
									onChange={(event) => {
										console.log("event", event.target.value);
										setRole(event.target.value);
									}}
								>
									{roles.map((roleItem) => (
										<MenuItem key={roleItem} value={roleItem}>
											{roleItem}
										</MenuItem>
									))}
								</Select>
							</FormControl>
						)}
						{column.id === "managerId" &&
							(role === UserRoles.MANAGER ? (
								textInput({ value: managerId, setValue: setManagerId })
							) : (
								<div></div>
							))}
						{column.id === "email" &&
							textInput({ value: email, setValue: setEmail })}
					</TableCell>
				))}
			</TableRow>
		</>
	);
}
